import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import {blue} from '@material-ui/core/colors';
import {TextField, Typography, Button} from "@material-ui/core";
import HelpIcon from '@material-ui/icons/Help';
import {post_data, put_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";
import DefaultImage from '../../static/default-image.jpg'
import ImageDialog from "../ImageDialog";

const useStyles = makeStyles({
    container: {
        padding: '1rem',
    },
    input: {
        width: '100%'
    },
    icon: {
        width: '100%'
    },
    footer: {
        display: 'flex',
        flexDirection: 'row',
        marginTop: '1rem',
        justifyContent: 'flex-end',
        padding: '1rem'
    },
    imageBanner: {
        width: '100%',
        height: '100%',
        objectFit : 'contain'
    }
});
export default function AddBannerDialog(props) {
    const {onClose, refresh_list, open} = props;
    const classes = useStyles();
    const [state, setState] = useState({
        id: 0,
        name: '',
        image_url: null
    })
    const handleAddBanner = () => {
        post_data(BACK_END_URL + `/admin/banner`, {}, {
            title: state.name,
            image_url: state.image_url
        }, true)
            .then(res => {
                refresh_list()
                onClose()
            })
            .catch(error => alert('Error when Edit Parent Category. try again later'))
    };
       const handleChange = (e) => {
        let name = e.target.name
        let val = e.target.value
        setState({
            ...state,
            [name]: val
        })
    }
    const handleImageChange = (e, name) => {
        const file = e.target.files[0]
        let url = URL.createObjectURL(file)
        setDataImage({
            name: name,
            src: url,
            aspect :  2/1
        })
        setOpenImage(true)
    }
    const handleSetImage = (e => {
        let name = e.name
        let value = e.url_image
        setState({
            ...state,
            image_url: value
        })
    })
    const handleImageClose = () => {
        setOpenImage(false)
    }
    const [openImage, setOpenImage] = useState(false)
      const [dataImage, setDataImage] = useState({
        name: null,
        src: null,
        aspect : 2/1
    })

    return (
        <Dialog aria-labelledby="add_banner-title" open={open} maxWidth={'sm'}>
            <DialogTitle id="add_banner-title">Add Category</DialogTitle>
            <div className={classes.container}>
                <Typography style={{marginTop: '1rem', marginBottom: '0.5rem'}}>Name category:</Typography>
                <TextField variant={'outlined'} placeholder={'Title banner'} required={true}
                           name='name' value={state.name} onChange={handleChange} className={classes.input}></TextField>
                <div style={{marginTop: "1rem", display: "flex", flexDirection : 'column', height: '300px', width: '450px',}}>
                    <input type={'file'} onChange={handleImageChange}/>
                    <img className={classes.imageBanner}
                         src={state.image_url == null ? DefaultImage : state.image_url}
                         alt={'image-banner'}/>
                </div>
            </div>
            <div className={classes.footer}>
                <Button variant={'contained'} color={'primary'} onClick={handleAddBanner}>Save</Button>
                <Button style={{marginLeft: '0.5rem'}} onClick={onClose}>Cancel</Button>
            </div>
            <ImageDialog data={dataImage} open={openImage} setPImage={handleSetImage} onClose = {handleImageClose} />
        </Dialog>
    );
}