import React, {useEffect, useState} from 'react';
import DialogTitle from "@material-ui/core/DialogTitle";
import {Button, TextField, Typography} from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import {makeStyles} from "@material-ui/core/styles";
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css'
import {post_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import CircularProgress from '@material-ui/core/CircularProgress';

const useStyle = makeStyles({
    root: {
        width: '500px',
        height: "100%"
    }
})
const ImageDialog = ({data, open, setPImage, onClose, aspect}) => {
    const classes = useStyle()
    const [crop, setCrop] = React.useState({
        aspect:data.aspect,
        x: 30,
        y: 30,
        width: 100 * data.aspect,
        height: 100,
    });
    useEffect(()=>{
        setCrop({
            ...crop,
            aspect: data.aspect
        })
    },[data.aspect])
    const [image, setImage] = useState(null)
    const handleComplete = (crop, pixelCrop) => {
        setCrop(crop)
    }

    const [loading, setLoading] = useState(false)

    async function handleOk() {
        const croppedImg = await getCroppedImg(image, crop, 'hello');
        var file = new File([croppedImg], 'test.png', {type: 'image/png', lastModified: Date.now()});
        const formData = new FormData();
        formData.append(
            "upload",
            file,
        );
        setLoading(true)
        post_data(BACK_END_URL + "/admin/image/uploads", {}, formData, true)
            .then(res => {
                    setPImage({
                        name: data.name,
                        url_image: res.data.urls.default
                    })
                    onClose()
                    setLoading(false)
                }
            )
            .catch(() => {
                alert('Error when upload file')
                setLoading(false)
            })
    }

    function getCroppedImg(image, crop, fileName) {
        const canvas = document.createElement("canvas");
        const scaleX = image.naturalWidth / image.width;
        const scaleY = image.naturalHeight / image.height;
        var originWidth = crop.width * scaleX;
        var originHeight = crop.height * scaleY;
        // maximum width/height
        var maxWidth = 1200, maxHeight = 1200 / aspect;
        var targetWidth = originWidth,
            targetHeight = originHeight;
        if (originWidth > maxWidth || originHeight > maxHeight) {
            if (originWidth / originHeight > maxWidth / maxHeight) {
                targetWidth = maxWidth;
                targetHeight = Math.round(maxWidth * (originHeight / originWidth));
            } else {
                targetHeight = maxHeight;
                targetWidth = Math.round(maxHeight * (originWidth / originHeight));
            }
        }
        // set canvas size
        canvas.width = targetWidth;
        canvas.height = targetHeight;
        const ctx = canvas.getContext("2d");

        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            targetWidth,
            targetHeight
        );

        return new Promise((resolve, reject) => {
            canvas.toBlob(
                blob => {
                    if (!blob) {
                        console.error("Canvas is empty");
                        return;
                    }
                    blob.name = fileName;
                    resolve(blob);
                },
                "image/png",
                1
            );
        });
    }

    const onImageLoaded = (image) => {
        setImage(image)
    }

    return (
        <Dialog aria-labelledby="add_image" open={open} maxWidth="md">
            <DialogTitle id="add_image">Chose size</DialogTitle>
            <div className={classes.root}>
                <ReactCrop style={{width: '100%'}}
                           onImageLoaded={onImageLoaded}
                           src={data.src}
                           crop={crop} onChange={newCrop => {
                    setCrop(newCrop)
                }}
                           onComplete={handleComplete}/>
                <div style={{float: 'right', margin: "1rem 1rem 1rem 0"}} >
                    <Button variant={'contained'} color={'primary'} onClick={handleOk}  disabled={loading?true:false}>{
                        loading? <CircularProgress color={'secondary'} size={25}/> : 'Save'
                    }</Button>
                    <Button style={{marginLeft: '0.5rem'}} onClick={()=>{onClose()}} disabled={loading?true:false}>Cancel</Button>
                </div>
            </div>
        </Dialog>
    );
};

export default ImageDialog;