import React from 'react';
import {get_data} from "./ApiCall";
import {BACK_END_URL} from "./ServiceURL";
import {fetch_user} from "./redux/Actions/ActionObjects/ActionsObjects";
import {useDispatch} from "react-redux";
import {theme as defaultTheme} from "./Themes";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import AppbarComponent from "./Component/Appbar.Component";
import MainComponent from "./Component/Main.Component";
import {MuiThemeProvider} from "@material-ui/core/styles";
const AuthComponent = () => {
    const [state, setState] = React.useState(true)
    React.useEffect(() => {
        if (localStorage.getItem('jwt') !== null) {
            setState(true)
            get_data(BACK_END_URL + '/admin/verify_login', {}, true)
                .then(res => {
                    let data = {
                        isAuthenticated: true,
                        id: res.data.user_id,
                        name: res.data.user_name,
                        email: res.data.user_email,
                        user_avatar:res.data.user_avatar,
                        is_admin: res.data.is_admin
                    }
                    dispatch(fetch_user(data))
                    setState(false)
                })
                .catch(error =>
                    setState(false)
                )
        } else {
            setState(false)
        }
    }, [])
    const dispatch = useDispatch()
    return (
        <div>
            {state === true ?
              null :
                <MuiThemeProvider theme={defaultTheme}>
                    <Router>
                        <AppbarComponent/>
                        <MainComponent/>
                    </Router>
                </MuiThemeProvider>
            }
        </div>
    );
};

export default AuthComponent;