import React, {useState} from 'react';
import clsx from 'clsx'
import {List, Divider, ListItem, ListItemText, Box} from '@material-ui/core'
import InboxIcon from '@material-ui/icons/MoveToInbox';
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {ListItemAvatar, Avatar} from '@material-ui/core';
import {Paper, Typography, Collapse} from '@material-ui/core';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import DashboardRoundedIcon from '@material-ui/icons/DashboardRounded';
import GroupRoundedIcon from '@material-ui/icons/GroupRounded';
import SettingsRoundedIcon from '@material-ui/icons/SettingsRounded';
import {makeStyles} from "@material-ui/core";
import StoreIcon from '@material-ui/icons/Store';
import ViewCarouselIcon from '@material-ui/icons/ViewCarousel';

const drawerWidth = 240;
const useDrawerStyles = makeStyles((theme) => ({
    drawer: {
        flexShrink: 0,
        whiteSpace: 'nowrap',
        overflowX: "hidden",
        backgroundColor: "white",
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
        width: theme.spacing(9) + 1,
        [theme.breakpoints.up("sm")]: {
            width: theme.spacing(9) + 1
        }
    },
    toggleButton: {
        position: "absolute",
        display: 'flex',
        bottom: 0,
        right: 0,
        marginBottom: "25px"
    },
    listIcon: {
        width: "40px"
    },
    center: {
        marginLeft: "9px"
    },
    expandPanelSumary: {
        display: "flex",
        justifyContent: "flex-start",
        left: 0,
        transform: "translateX(-22px)"

    },
    expandPanel: {
        boxShadow: "none",
        backgroundColor: "transparent",
        spacing: 0
    },
    divider: {
        backgroundColor: "white",
    },
    collapse: {},
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    toolbar: theme.mixins.toolbar,
}));
export default function DrawerComponent() {
    const classes = useDrawerStyles();
    const history = useHistory();
    const [expanded, setExpanded] = useState(false);
    const [expanded2, setExpanded2] = useState(false)
    const handleItemClick = (id) => {
        switch (id) {
            case 1:
                history.push("/");
                break;
            default:
                break;
        }
    }

    const handleExpandCategory= () => {
        setExpanded2(!expanded2)
    }

    const handleExpandProduct = () => {
        setExpanded(!expanded);
    }

    const handleClickCategory = () => {
        history.push('/par_category')
    }

    const handleClickSubProduct = () => {
        history.push('/category')
    }
    const handleClickAddProduct = () => {
        history.push("/add_product");
    }
    const handleClickProduct = () => {
        history.push("/products");
    }
    const admin = useSelector(state => state.AuthenReducer)
    return (
        <Paper style={{height: "100%", overflow: 'auto', width: "240px", position: "fixed"}} square>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar
                            alt={`123`}
                            src={admin.user_avatar}
                        />
                    </ListItemAvatar>
                    <ListItemText primary={admin.name}/>
                </ListItem>
                <Divider variant="middle" classes={{root: classes.divider}}/>

                <ListItem button className={classes.listItem} onClick={() => handleItemClick(1)}>
                    <DashboardRoundedIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Dashboard</Typography>}/>
                </ListItem>

                <ListItem button className={classes.listItem} onClick={handleExpandCategory}>
                    <GroupRoundedIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Category</Typography>}/>
                    <ExpandMoreIcon className={clsx(classes.expand, {
                        [classes.expandOpen]: expanded2,
                    })}/>
                </ListItem>
                <Collapse in={expanded2} timeout="auto" unmountOnExit>
                    <ListItem button className={classes.listItem} onClick={handleClickCategory}>
                        <Box className={classes.center} style={{marginRight: "20px", width: "20px"}}/>
                        <ListItemText primary={<Typography color="textPrimary">Category</Typography>}/>
                    </ListItem>
                    <ListItem button className={classes.listItem} onClick={handleClickSubProduct}>
                        <Box className={classes.center} style={{marginRight: "20px", width: "20px"}}/>
                        <ListItemText primary={<Typography color="textPrimary">Sub Category</Typography>}/>
                    </ListItem>
                </Collapse>

                <ListItem button className={classes.listItem} onClick={handleExpandProduct}>
                    <SettingsRoundedIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Product</Typography>}/>
                    <ExpandMoreIcon className={clsx(classes.expand, {
                        [classes.expandOpen]: expanded,
                    })}/>
                </ListItem>
                <Collapse in={expanded} timeout="auto" unmountOnExit>
                    <ListItem button className={classes.listItem} onClick={handleClickProduct}>
                        <Box className={classes.center} style={{marginRight: "20px", width: "20px"}}/>
                        <ListItemText primary={<Typography color="textPrimary">All Product</Typography>}/>
                    </ListItem>
                    <ListItem button className={classes.listItem} onClick={handleClickAddProduct}>
                        <Box className={classes.center} style={{marginRight: "20px", width: "20px"}}/>
                        <ListItemText primary={<Typography color="textPrimary">Add Product</Typography>}/>
                    </ListItem>
                </Collapse>
                <ListItem button className={classes.listItem} onClick={()=>history.push('/branch')}>
                    <StoreIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Branch</Typography>}/>
                </ListItem>
                <ListItem button className={classes.listItem}>
                    <InboxIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Orders</Typography>}/>
                </ListItem>
                <ListItem button className={classes.listItem} onClick={()=>history.push('/banner')}>
                    <ViewCarouselIcon className={classes.center} style={{marginRight: "20px"}}/>
                    <ListItemText primary={<Typography color="textPrimary">Banner</Typography>}/>
                </ListItem>
            </List>
        </Paper>
    );
}