import React from 'react';
import {Typography, Button} from "@material-ui/core";
import AddIcon from '@material-ui/icons/Add';
import {get_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import SimpleTable from "../Component/ParentCategory/Table.ParCategory";
import SimpleDialog from "../Component/ParentCategory/Dialog.AddParCategory"
import BannerTable from "../Component/Banner/Table.Banner";
import AddBannerDialog from "../Component/Banner/Dialog.AddBanner";
const BannerPage = () => {
    const [banner, setBanner] = React.useState(0)
    const [listBanner, setListBanner] = React.useState([])
    const [openAddDialog, setOpenAddDialog] = React.useState(false)
    React.useEffect(() => {
        get_data(BACK_END_URL + '/public/banner', {page: 1}, false)
            .then(res => setListBanner(res.data.list))
    }, [])
    const refresh_list = () => {
         get_data(BACK_END_URL + '/public/banner', {page: 1}, false)
            .then(res => setListBanner(res.data.list))
    }
    return (
        <div style={{padding: '2rem'}}>
            <Typography variant={'h4'}>Banner</Typography>
            <div style={{display :'flex', flexDirection : "row" , marginTop: '2rem'}}>
                <Button variant={'contained'} color={'primary'} style={{height : '3rem'}}
                onClick={()=>{setOpenAddDialog(true)}}>
                    <AddIcon/>
                    Add banner</Button>
            </div>
            <BannerTable data = {listBanner} refresh_list = {refresh_list}/>
            <AddBannerDialog open = {openAddDialog} onClose = {()=>setOpenAddDialog(false)} refresh_list = {refresh_list}/>
        </div>
    );
};

export default BannerPage;