import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import {blue} from '@material-ui/core/colors';
import {Divider, Typography, Button} from "@material-ui/core";

const useStyles = makeStyles({
    avatar: {
        backgroundColor: blue[100],
        color: blue[600],
    },
    container: {
        padding: '0 1rem 1rem 1rem',
        minWidth: '300px'
    },
    input: {
        width: '100%'
    },
    icon: {
        width: '100%'
    },
    footer: {
        display: 'flex',
        flexDirection: 'row',
        marginTop: '1rem',
        justifyContent: 'flex-end'
    }
});
export default function DeleteDialog(props) {
    const classes = useStyles();
    const [state, setState] = useState({
        name: '',
        icon_class: ''
    })
    const {onClose, handleDelete, open} = props;
    return (
        <Dialog aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle id="simple-dialog-title">Delete</DialogTitle>
            <Divider variant={'middle'}/>
            <div className={classes.container}>
                <Typography style={{marginTop: '1rem', marginBottom: '0.5rem'}}>Are you sure to delete this?</Typography>
                <div className={classes.footer}>
                    <Button variant={'contained'} color={'primary'} onClick={handleDelete}>Delete</Button>
                    <Button style={{marginLeft: '0.5rem'}} onClick={onClose}>Cancel</Button>
                </div>
            </div>
        </Dialog>
    );
}