import React from 'react';
import {AppBar, Toolbar, IconButton, Typography, Button} from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import {makeStyles} from "@material-ui/core/styles";
import {useHistory} from 'react-router-dom'
import {useSelector, useDispatch} from "react-redux";
import {fetch_user} from "../redux/Actions/ActionObjects/ActionsObjects";
import {Input, InputAdornment, Divider, Tooltip} from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import {withStyles} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    toolbar: {
        width: '98%',
        padding: 0,
        margin: 'auto'
    },
    title: {
        flexGrow: 1
    },
    link: {
        marginRight: "1rem",
        border: '1px solid white'
    },
    search: {
        marginLeft: '1rem',
        paddingLeft: '0.2rem',
        borderRadius: '2rem',
        color: 'white',
        backgroundColor: 'rgba(255, 255, 255, .1)',
        width: '15rem',
        maxWidth: '100rem',
        marginRight: '1rem',
        paddingRight: '1rem',
        '&:hover': {
            backgroundColor: 'rgba(255, 255, 255, .3)'
        }
    },
}));

export const HtmlTooltip = withStyles((theme) => ({
    tooltip: {
        backgroundColor: '#f5f5f9',
        color: 'rgba(0, 0, 0, 0.87)',
        maxWidth: 220,
        fontSize: theme.typography.pxToRem(12),
        border: '1px solid #dadde9',
    },
    arrow: {
        color: "#f5f5f9"
    }
}))(Tooltip);

const AppbarComponent = () => {
    const classes = useStyles();
    const history = useHistory()
    const dispatch = useDispatch()
    const [state, setState] = React.useState({
        search: ''
    })

    async function handleClick(e, index) {
        e.preventDefault()
        if (index === 1) history.push('/login')
        else if (index === 2) history.push('/signup')
        else if (index === 3) history.push('/add_post')
        else if (index === 4) {
            history.push(`/profile/${id}`)
        } else if (index === 5) {
            localStorage.removeItem('jwt')
            let data = {
                isAuthenticated: false,
                id: 0,
                name: '',
                email: ''
            }
            dispatch(fetch_user(data))
        } else {
            history.push('/all_tags')
        }
    }

    const handleHome = () => {
        history.push('/')
    }
    const handleKeyDown = (e) => {
        if (e.key === 'Enter') {
            history.push(`/search?query=${state.search}`)
            setState({...state, search: ''})
        }
    }
    const handleChange = (e) => {
        let name = e.target.name
        let val = e.target.value
        setState({
            ...state,
            [name]: val
        })
    }
    const [isDrawOpen, setDrawOpen] = React.useState(false)
    const {isAuthenticated, id, user_avatar, name} = useSelector(state => state.AuthenReducer)
    const toggleDrawer = () => {
        setDrawOpen(true)
    }
    const handleCloseDraw = () => {
        setDrawOpen(false)
    }
    return (
        <AppBar position="fixed" style={{boxShadow: 'none', paddingLeft: '1rem', minWidth: '900px'}}
                color={'secondary'}>
            <Toolbar className={classes.toolbar}>
                <Button onClick={handleHome}><Typography variant="h5"
                                                         style={{
                                                             color: "white",
                                                             fontWeight: "bold"
                                                         }}>DASHBOARD</Typography></Button>
                <div className={classes.title}>
                </div>
                <div>
                    {
                        isAuthenticated === false ?
                            <div>
                                <Button color="inherit" className={classes.link}
                                        onClick={(e) => handleClick(e, 1)}>
                                    Login
                                </Button>
                            </div> :
                            <div style={{display: 'flex', flexDirection: 'row'}}>
                                <Button color="inherit" className={classes.link}
                                        onClick={(e) => handleClick(e, 5)}>
                                    Logout
                                </Button>
                            </div>
                    }
                </div>
            </Toolbar>
        </AppBar>
    );
};

export default AppbarComponent;