import React, {useState} from 'react';
import {TextField, makeStyles, Paper, Typography, Button, Divider, useMediaQuery} from "@material-ui/core";
import axios from 'axios'
import {BACK_END_URL} from '../ServiceURL'
import {useHistory} from 'react-router-dom'
import {useDispatch} from "react-redux";
import {fetch_user, open_notification} from "../redux/Actions/ActionObjects/ActionsObjects";
import {useSelector} from "react-redux";
import {useLocation, NavLink, Redirect} from 'react-router-dom'
import {theme} from "../Themes";

const useStyle = makeStyles(theme => ({
    container: {
        display: "flex",
        flexDirection: "column",
        margin: props => props.isMobile ? "2rem auto" : '6rem auto',
        width: "100%",
        maxWidth: '25rem',
        '&>*': {
            marginTop: '1rem'
        },
    },
    input: {
        borderRadius: 'none',
        width: '100%'
    },
    buttonLogin: {
        boxShadow: 'none',
        margin: 'auto',
        padding : '0.7rem 1rem 0.7rem 1rem',
        alignSelf : 'center',
        '&:hover': {
            boxShadow: 'none'
        },
        width : '100%'
    },
    title: {
        width: '100%',
        textAlign: 'center',
    },
    main: {
        padding: '1rem',
        '&>*': {
            marginTop: '1rem'
        }
    }
}))


const LoginPage = () => {
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    const classes = useStyle({isMobile});
    const [state, setState] = useState({
        username: '',
        password: '',
        isOpen: false,
        emailPass: '',
        emailConfirm: '',
        isSent: 0,
        resMessage: '',
        is_confirm_open: false
    })
    const history = useHistory()
    const location = useLocation()
    const handleChange = (e) => {
        let value = e.target.value
        let name = e.target.name
        setState({...state, [name]: value})
    }
    const dispatch = useDispatch()
    const handleClick = (e) => {
        axios.post(BACK_END_URL + '/admin/authenticate', state)
            .then(res => {
                localStorage.setItem('jwt', res.data.jwt)
                let data = {
                    isAuthenticated: true,
                    id: res.data.user_id,
                    email: res.data.user_email,
                    name: res.data.user_name,
                    user_avatar: res.data.user_avatar,
                    is_admin : res.data.is_admin
                }
                dispatch(fetch_user(data))
                if (location.state)
                    history.push(location.state.nextUrl)
                else
                    history.push('/')
                dispatch(open_notification({message: 'Login Success', type: 'success'}))
            })
            .catch(error => {})
    }
    const {isAuthenticated} = useSelector(state => state.AuthenReducer)

    return (
        <div>
            {
                isAuthenticated === false ?
                    < div className={classes.container}>
                        <Typography variant={"h5"} className={classes.title}>LOGIN</Typography>
                        <div className={classes.main}>
                            <TextField required id="username" label="Username" type="text" name='username'
                                       variant={'outlined'} className={classes.input}
                                       onChange={handleChange}/>
                            <TextField required id="password" label="Password" type="password" name='password'
                                       variant={'outlined'} className={classes.input}
                                       onChange={handleChange}/>
                            <div style={{width :'100%', textAlign :'center'}}>
                                <Button variant="contained" color="secondary" onClick={handleClick}
                                        className={classes.buttonLogin}> Login</Button>
                            </div>
                        </div>
                    </div> : <Redirect to='/'/>
            }
        </div>
    );
};

export default LoginPage;